const state = {
  dorpDownDetail: {
    id: "",
    name: "",
    desc: "",
    permission: "",
  },
};

const mutations = {
  switchDetail(state, val) {
    state.dorpDownDetail = {
      id: val.id,
      name: val.name,
      desc: val.desc,
      permission: val.permission,
    };
  },
};


export default {
  namespaced: true,
  state,
  mutations,
};
